
body {
    background:
           /* top, transparent black, faked with gradient */ 
           linear-gradient(
             rgba(0, 0, 0, 0.7), 
             rgba(0, 0, 0, 0.7)
           ),
           /* bottom, image */
           url("../images/other/background.webp");
           overflow-x: hidden;
}

@media(min-width:501px) 
{
  .logo{
    position: absolute;
    left: 56%;
    top: 40%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  .logoimg{
      width:100%;
      max-width:60%; 
  }
  .center{
    align-self:center;
    position: absolute;
    right: 59%;
  
   
    padding: 10px;
  }
} 

@media(max-width:500px) 
{
  .logo{
    position: absolute;
    left: 50%;
    top: 40%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  .logoimg{
      width:100%;
      
  }
  
}
.cards{
  display: flex;
  flex: 1;
  overflow-x: hidden;
  position: fixed;
  bottom: 0;
  width: 100%;
}
#loadingHome{
  position: absolute;
  left: 45%;
  bottom: 100px;
}


a:hover {
  color:white;
}

p, h1, h2, h3,h4{ 
  color: rgba(255, 255, 255, 0.8); 
  font-variant-caps: normal;
}  


html::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

html::-webkit-scrollbar-thumb
{

	box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;}
.style-1::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

.style-1::-webkit-scrollbar-thumb
{

	box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;}


